import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Table } from "react-bootstrap";


const schedule = [
  {
    id: 1,
    day: "",
    subject: " ",
    teacher: " ",
    time: "9:00 - 10:00",
  },
  {
    id: 2,
    day: "",
    subject: "",
    teacher: " ",
    time: "10:00 - 11:123",
  },
  {
    id: 3,
    day: "",
    subject: "",
    teacher: " ",
    time: "11:00 - 12:00",
  },
];
function CoursesTable() {
    const dates = [
      "",
      "",
      "",
      "",
      "",
      "",
    ];
 

 
  return (
    <div>
      <h2 className="drived-page-title"> </h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th></th>
            <th>8-10 </th>
            <th>10-12</th>
            <th>12-2 </th>
            
          </tr>
        </thead>
        <tbody>
          {schedule.map((day, index) => (
            <tr key={index}>
              <td>{dates[index]}</td>
              <td>
                {
                  <>
                    <div>{day.subject}</div>
                  
                  </>
                }
              </td>
              <td></td>
              <td></td>
              
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}
export default CoursesTable
