import React from 'react'
import { Container, Row, Col } from "react-bootstrap";
function Condations() {
 const conditions = [
   { text: "         ." },
   { text: "      (   )." },
   {
     text: "      (    ).",
   },
   {
     text: "       (    ).",
   },
   {
     text: "       (    ).",
   },
   { text: "       ." },
   { text: "      ." },
   { text: "     ." },
   { text: "      ." },
   { text: "      ." },
   { text: "      ." },
   { text: "      ." },
 ];

 return (
   <div className='conditions'>
     <Container>
       <h1 className='main-header'>     -  </h1>
       <Row>
         <Col>
           <ul>
             {conditions.map((condition, index) => (
               <li key={index}>{condition.text}</li>
             ))}
           </ul>
         </Col>
       </Row>
     </Container>
   </div>
 );
}

export default Condations
