import { useState } from "react";

let departments = [
  { id: 1, name: " " },
  { id: 2, name: " " },
  { id: 3, name: "  " },
  { id: 4, name: " " },
  { id: 5, name: "  " },
  { id: 6, name: " " },
  { id: 7, name: " " },
  
]
function AplyAplication() {
  const[allowed,setAllowed]=useState(true)

  return (
    <div className="application-form">
      <div className="container">
        <h1 className="main-header">   </h1>
        {allowed=== false ? (
          <h2 className="main-header">  </h2>
        ) : (
          <form id="application-form">
            <div className="row">
              <div className="form-group col-md-4">
                <label htmlFor="studentName"> </label>
                <input
                  type="text"
                  className="form-control"
                  id="studentName"
                  name="fullName"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="studentName"> </label>
                <input
                  type="text"
                  className="form-control"
                  id="studentName"
                  name="NID"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="studentEmail"> </label>
                <input
                  type="email"
                  className="form-control"
                  id="studentEmail"
                  name="email"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="studentPhone"> </label>
                <input
                  type="tel"
                  className="form-control"
                  id="studentPhone"
                  pattern="[0-9]{3}-?[0-9]{2}-?[0-9]{3}"
                  title="     123-456-7890"
                  name="phoneNumber"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="qualification"> </label>
                <input
                  type="text"
                  className="form-control"
                  id="qualification"
                  name="qualification"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="qualificationDate"> </label>
                <input
                  type="date"
                  className="form-control"
                  id="qualificationDate"
                  name="qualificationDate"
                />
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="college"></label>
                <select className="form-control" id="college" name="college">
                  <option value=""> </option>
                  <option value="computer-science"> </option>
                  <option value="engineering">  </option>
                  <option value="engineering"> </option>
                  <option value="commerce"> </option>
                  <option value="law"> </option>
                  <option value="medicine"> </option>
                  <option value="pharmacy"> </option>
                  <option value="dentistry">  </option>
                  <option value="nursing"> </option>
                  <option value="education"> </option>
                </select>
              </div>

              <div className="form-group col-md-4">
                <label htmlFor="program">  </label>
                <select className="form-control" id="program" name="program">
                  <option value="">   </option>
                  <option value="master"></option>
                  <option value="phd"></option>
                </select>
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="specialization"></label>
                <select
                  className="form-control"
                  id="specialization"
                    name="departmentId"
                    
                >
                  <option value=""> </option>
                  {departments.map((item, index) => (
                    <option value={item.id} key={index} >
                      {item.name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="form-group col-md-4">
                <label htmlFor="militaryStatus">  </label>
                <select
                  className="form-control"
                  id="militaryStatus"
                  name="militaryStatus"
                >
                  <option value="">   </option>
                  <option value="completed"></option>
                  <option value="postponed"></option>
                  <option value="exempt"></option>
                </select>
              </div>
              <div className="form-group col-md-4">
                <label htmlFor="fileUpload"> </label>
                <input
                  type="file"
                  className="form-control-file"
                  id="fileUpload"
                  name="profilePicture"
                />
              </div>
              <div className="form-group col-md-4">
                <button
                  type="submit"
                  className="btn btn-primary w-100 mt-4"
                  
                >
                  
                </button>
              </div>
            </div>
          </form>
        )}
      </div>
    </div>
  );
}

export default AplyAplication
