import React, { useEffect, useState } from "react";
import "../../styles/admin/main.css"


let departments = [
  { id: 1, name: " " },
  { id: 2, name: " " },
  { id: 3, name: "  " },
  { id: 4, name: " " },
  { id: 5, name: "  " },
  { id: 6, name: " " },
  { id: 7, name: " " },
];



function AddCourse() {


  const handleSubmit = async(e) => {
alert("add successfully")
  }
 
  return (
    <div>
      <div className="container">
        <h2 className="drived-page-title">  </h2>
        <form className="add-course" dir="auto" id="newCourse-form">
          {" "}
          <div className="form-group arname">
            <label htmlFor="courseName"> </label>
            <input
              type="text"
              className="form-control"
              id="courseName"
              name="courseNameArabic"
            />
          </div>
          <div className="form-group enname">
            <label htmlFor="EncourseName">(English) </label>
            <input
              type="text"
              className="form-control"
              id="EncourseName"
              name="courseName"
            />
          </div>
          <div className="form-group houers">
            <label htmlFor="courseHours "> </label>
            <input
              type="number"
              className="form-control"
              id="courseHours"
            name="numberHours"
            />
          </div>
          <div className="form-group min">
            <label htmlFor="courseSmallGrade"> </label>
            <input
              type="number"
              className="form-control"
              id="courseSmallGrade"
            name="minorEnd"
            />
          </div>
          <div className="form-group dep">
            <label htmlFor="department"></label>
            <select
              className="form-control"
              id="department"
             name="departmentDepartmentId"
            >
              {departments.map((item, index) => (
                <option value={item.id} key={index}>
                  {item.name}
                </option>
              ))}
            </select>
          </div>
          <div className="form-group max">
            <label htmlFor="courseBigGrade "> </label>
            <input
              type="number"
              className="form-control"
              id="courseBigGrade"
             name="greatEnd"
            />
          </div>
          <div className="form-group Encode">
            <label htmlFor="courseCodeEn "> (English)</label>
            <input
              type="text"
              className="form-control"
              id="courseCodeEn"
            name="courseId"
            />
          </div>
          <div className="form-group level">
            <label htmlFor="courseLevel"> </label>
            <select
              className="form-control"
              id="courseLevel"
             name="levelId"
            >
              <option value={1}>  </option>
              <option value={2}>  </option>
              <option value={3}>  </option>
            </select>
          </div>
          <button
            type="submit"
            className="btn btn-primary add h-50 mt-5"
            onClick={handleSubmit}
          >
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default AddCourse;
