import React from 'react'

function ContactUs() {
  return (
    <div className="contact-us">
      <div className="container">
        <h1 className="main-header"> </h1>
        <div className="row">
          <div className="col-md-6">
            <form className='contact-form'>
              <div className="form-group">
                <label htmlFor="formName" className="my-2">
                   
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="formName"
                  placeholder="  "
                />
              </div>
              <div className="form-group">
                <label htmlFor="formEmail" className="my-2">
                   
                </label>
                <input
                  type="email"
                  className="form-control"
                  id="formEmail"
                  placeholder="  "
                />
              </div>
              <div className="form-group">
                <label htmlFor="formPhone" className="my-2">
                   
                </label>
                <input
                  type="tel"
                  className="form-control"
                  id="formPhone"
                  placeholder="  "
                />
              </div>
              <div className="form-group">
                <label htmlFor="formMessage" className="my-2">
                  
                </label>
                <textarea
                  className="form-control"
                  id="formMessage"
                  placeholder=" "
                ></textarea>
              </div>
              <button type="submit" className="btn btn-primary my-3">
                
              </button>
            </form>
          </div>
          <div className="col-md-6">
            <h2> </h2>
            <p>     </p>
            <p>: 0123456789</p>
            <p>
               :{" "}
              <a href="mailto:info@college.edu">info@college.edu</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ContactUs
