import React from 'react'
import { Table } from "react-bootstrap";
import "../../styles/student/mainInfo.css"
const student = {
  id: 1,
  name: " ",
  email: "john.doe@example.com",
  age: 18,
  gpa: 3.5,
  credits: 120,
  college: " ",
  major: "Major 1",
};


function MainInfo() {







  
  return (
    <div dir="rtl">
      <h2 className='my-3 drived-page-title'> </h2>
      <Table striped bordered hover>
        <tbody>
          <tr>
            <td>
              <h4>:</h4>
            </td>
            <td>{student.name}</td>
          </tr>
          <tr>
            <td>
              <h4> :</h4>
            </td>
            <td>{student.email}</td>
          </tr>
          <tr>
            <td>
              <h4>:</h4>
            </td>
            <td>{student.age}</td>
          </tr>
          <tr>
            <td>
              <h4> :</h4>
            </td>
            <td>{student.gpa}</td>
          </tr>
          
          <tr>
            <td>
              <h4>:</h4>
            </td>
            <td>{student.college}</td>
          </tr>
          <tr>
            <td colSpan={2}>
              <h4>:</h4> {student.major}
            </td>
          </tr>
        </tbody>
      </Table>
    </div>
  );
}

export default MainInfo
